				}
			]
			
			uni.hideLoading()
			}, 300)
		},
		//获取页面数据
		getlist() {
			let url = "/graphic/selection";
			if(this.activeIndex == 1) {
				url = "/graphic/intro";
			}
			common.requestURL({
				url: url,
				method: 'GET',
			}).then( res => {
				this.activeArray = res.data;
			})
		},
		
